@import url(https://fonts.googleapis.com/css?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css?family=Cabin+Sketch:700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{
    padding: 0;
    margin: 0;
}

.header{
    width: 100%;
    text-align: center;
    background-color: #273140;
    position: fixed;
}

.logo{
    font-family: 'Lobster';
    font-size: 40px;
    padding: 10px;
}
.first{
    color:#fff
}
.second{
    color:#009688
}
*{
    padding: 0;
    margin: 0;
}

.footer{
    width: 100%;
    text-align: center;
    background-color: #2c2c2c;
    position: fixed;
    left: 0;
    bottom: 0;
}

.created{
    color:#fff;
    font-family: 'Lobster';
    font-size: 20px;
    padding: 10px;
}

.card{
    width: 300px;
    height: 300px;
    background-color: #7a7a7a;
    color:#fff;
    margin:0 18px!important;
    text-align: center;
    border-radius: 15px;
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
    display: flex;
    flex-direction: column;
}
.card-title{
    font-family: 'Roboto';
    font-size: 20px;
    padding: 10px 0;
}
.card-btn1{
    width: 150px;
    background-color: #fff;
    color:#000;
    padding: 10px 20px;
    border-radius: 15px;
    margin: auto auto 20px auto;
}
.card-btn:hover{
    background-color: rgb(167, 167, 167);
    cursor: pointer;
}
.card-time{
    margin: 10px 0;
}
.card-text{
    width: 80%;
    margin: 0 auto;
    text-overflow: ellipsis; 
    overflow: hidden;
    word-break: break-word;
}
@media only screen and (max-height: 600px) {
    .card{
        height: 200px!important;
        margin: 20px 0!important;
    }
}

.card-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 20px;
}
.pagination{
    display: flex;
    list-style: none;
    justify-content: center;
}
.pagination button{
    padding: 10px;
    border:solid 1px #000;
    
}
.active button,.active:hover button{
    color:#000;
    background-color: rgb(104, 104, 104);
}
.pagination button:hover{
    cursor: pointer;
    background-color: rgb(160, 160, 160);
}
@media only screen and (max-width: 500px) {
    .pagination{
        padding-bottom: 100px;
        flex-wrap: wrap;
    }
}
.main-container{
    padding-top: 80px;
    display: flex;
    flex-direction: column;
    height: 90vh;
}
.listPost{
    flex-grow: 1;
}
.functional-container{
    display: flex;
    justify-content: space-between;
}

.add-link{
    margin: auto 0;
}
@media only screen and (max-width: 500px) {
    .functional-container{
        flex-direction: column
    }
    .add-link{
        width: 100px;
        margin: 0 auto;
    
    }
}
.input-container{
    display: flex;
    flex-direction: column;
    padding: 10px;
    margin: 10px;
    border-radius: 20px;
    color:#fff;
    text-align: center;
    background-color: rgb(62, 62, 85);
}
.input{
    padding: 5px;
    margin: 10px;
}
.input-main{
    display: flex;
}
.btn-group{
    display: flex;
}
@media only screen and (max-width: 500px) {
    .btn-group{
        flex-direction: column;
    }
}
.btn{  
    height: 40px;
    font-size: 15px;  
}
.btn-search,.btn-clear{
    margin: auto;
}
.search,.clear,.post{
    border-radius: 20px;
    width: 70px;
    background-color: rgb(97, 88, 102);
    color: #fff;
}
.search:hover,.add:hover,.clear:hover,.post:hover{
    background-color: rgb(52, 49, 53);
    cursor: pointer;
}
.btn-add{
    margin: auto 0;
}
.add{
    border-radius: 20px;
    width: 100px;
    background-color: rgb(97, 88, 102);
    color: #fff;
}
.add-post_container{
    display: flex;
    justify-content: space-around;
    padding-top: 80px;
}
.see-post{
    width: 25%;
    text-align: center;
    word-break: break-all;
}
.card{
    width: 300px;
    height: 300px;
    background-color: #7a7a7a;
    color:#fff;
    margin:0 20px;
    text-align: center;
    border-radius: 15px;
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
    display: flex;
    flex-direction: column;
}
.card-title1{
    font-family: 'Roboto';
    font-size: 20px;

}
.card-btn{
    width: 150px;
    background-color: #fff;
    color:#000;
    padding: 10px 20px;
    margin: 10px 0;
    border-radius: 15px;
    margin: auto auto 0 auto;
}
.card-btn1:hover{
    background-color: rgb(167, 167, 167);
    cursor: pointer;
}
.card-text1{
    text-overflow: ellipsis; 
}
.new-post{
    width: 20%;
}
@media only screen and (max-width:501px){
    .add-post_container{
        display: flex;
        flex-direction: column;
    }
    .new-post,.see-post{
        width: 90%;
        margin: 0 auto;
    }
    .btn-container{
        display: flex;
        padding-bottom: 80px;
        justify-content: center;
    }
}
.post-container{
    padding-top: 100px;
    display: flex;
    justify-content: space-around;
}
.item-group-btn{
    display: flex;
    flex-direction: column;
}
.post-info{
    width: 35%;
}
.post-info_title,.post-info_description{
    font-size: 20px;
}
.post-info_title-text,.post-info_description-text{
    background-color: darkgrey;
    padding: 10px;
    border-radius: 20px;
}
.post-info_description-text{
    height: 125px;
    overflow: auto;
    margin-bottom: 5px;
}
.similar-post_item{
    background-color: darkgrey;
    padding: 10px;
    border-radius: 15px;
}
.similar-post_title{
    padding-bottom: 10px;
    font-weight: 600;
}
.similar-post_btn{
    width: 100px;
    background-color: #fff;
    color:#000;
    padding: 5px;
    border-radius: 15px;
}
.item-group-btn--save,
.item-group-btn--delete,
.item-group-btn--update,
.item-group-btn--cancel,
.item-group-btn--home{
    width: 150px;
    background-color: #fff;
    color:#000;
    padding: 5px;
    border-radius: 15px;
    margin: 20px;
}
.similar-post_btn:hover,
.item-group-btn--save:hover,
.item-group-btn--delete:hover,
.item-group-btn--update:hover,
.item-group-btn--cancel:hover,
.item-group-btn--home:hover{
    background-color: rgb(196, 196, 196);
    cursor: pointer;
}
@media only screen and (max-height: 600px) {
    .post-container{
        flex-direction: column;
    }
    .post-info,.similar-post{
        width: 75%;
        margin: 0 auto;
    }
    .item-group-btn{
        display: flex;
        flex-direction: row;
        padding-bottom: 60px;
    }
    .post-info_textarea{
        width: 90%;
    }
    .item-group-btn--save,
    .item-group-btn--delete,
    .item-group-btn--update,
    .item-group-btn--cancel,
    .item-group-btn--home{
        width: 100px;
        margin: 5px;
    }
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
.btn--home{
  text-decoration: none;
  font-size: 2rem;
  padding-bottom: 70px;
}

  .container-notF {
    font-family: 'Cabin Sketch', cursive;
    background-color: #ededed;
    overflow: hidden;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    color: #9779b3;
    text-shadow: 0 2px 5px #9779b3;
    padding-top: 80px;
  }
  .container-notF h1 {
    font-size: 4rem;
    text-transform: uppercase;
  }
  .container-notF .monkey-hammer-hat {
    width: 250px;
  }
  .container-notF .monkey-hammer-hat #arm {
    transform: translateX(0) translateY(0);
    -webkit-animation: fix-issue 1.5s 1.5s cubic-bezier(0.17, 0.67, 0.6, 1.33) infinite;
            animation: fix-issue 1.5s 1.5s cubic-bezier(0.17, 0.67, 0.6, 1.33) infinite;
  }
  .container-notF h2 {
    font-size: 2.5rem;
    text-transform: uppercase;
  }
  @media only screen and (max-width: 600px) {
    .container-notF h1 {
      display: none;
    }
    .container-notF h2 {
      font-size: 2rem;
      margin: 0 1rem;
    }
  }
  @-webkit-keyframes fix-issue {
    40% {
      transform: translateX(-2px) translateY(-1.5px);
    }
    75%, 100% {
      transform: translateX(0) translateY(0);
    }
  }
  @keyframes fix-issue {
    40% {
      transform: translateX(-2px) translateY(-1.5px);
    }
    75%, 100% {
      transform: translateX(0) translateY(0);
    }
  }
