@import url('https://fonts.googleapis.com/css?family=Cabin+Sketch:700'); 
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
.btn--home{
  text-decoration: none;
  font-size: 2rem;
  padding-bottom: 70px;
}

  .container-notF {
    font-family: 'Cabin Sketch', cursive;
    background-color: #ededed;
    overflow: hidden;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    color: #9779b3;
    text-shadow: 0 2px 5px #9779b3;
    padding-top: 80px;
  }
  .container-notF h1 {
    font-size: 4rem;
    text-transform: uppercase;
  }
  .container-notF .monkey-hammer-hat {
    width: 250px;
  }
  .container-notF .monkey-hammer-hat #arm {
    transform: translateX(0) translateY(0);
    animation: fix-issue 1.5s 1.5s cubic-bezier(0.17, 0.67, 0.6, 1.33) infinite;
  }
  .container-notF h2 {
    font-size: 2.5rem;
    text-transform: uppercase;
  }
  @media only screen and (max-width: 600px) {
    .container-notF h1 {
      display: none;
    }
    .container-notF h2 {
      font-size: 2rem;
      margin: 0 1rem;
    }
  }
  @-moz-keyframes fix-issue {
    40% {
      transform: translateX(-2px) translateY(-1.5px);
    }
    75%, 100% {
      transform: translateX(0) translateY(0);
    }
  }
  @-webkit-keyframes fix-issue {
    40% {
      transform: translateX(-2px) translateY(-1.5px);
    }
    75%, 100% {
      transform: translateX(0) translateY(0);
    }
  }
  @-o-keyframes fix-issue {
    40% {
      transform: translateX(-2px) translateY(-1.5px);
    }
    75%, 100% {
      transform: translateX(0) translateY(0);
    }
  }
  @keyframes fix-issue {
    40% {
      transform: translateX(-2px) translateY(-1.5px);
    }
    75%, 100% {
      transform: translateX(0) translateY(0);
    }
  }